<template>
	<!--意见反馈-->
	<div class="realize">
		<evaluate-input
	    :picArr="picArr"
	    @limitinput="limitinput">
	    </evaluate-input>
	    <div class="submitButton" @touchstart.prevent="submitEvaluate">
	    	<span>提交</span>
	    </div>
	</div>
</template>
<script type="ecmascript-6">
    import tools from 'utils/tools'
    import {mapGetters} from 'vuex'
	import {setRealize} from 'api/repair'
    import {closeWindow} from 'api/global'
	import evaluateInput from '../repairs/common/evaluateInput'
	import { Toast } from 'mint-ui'
    export default {
    	name:'realize',
		components:{
			"evaluate-input":evaluateInput
		},
		data(){return{
			picArr:[],
			limitInput:'',
	        "remark": [//用于提交的信息
		        {
		            "name": "1",
		            "value": "文字"
		        }
	    	]
		}},
        computed:{
            ...mapGetters([
                'upload_token'
            ]),
        },
		methods:{
			limitinput(val){
    			this.limitInput=val
    		},
    		submitEvaluate(){
    			if(this.limitInput.length){
    				this.remark[0].value=this.limitInput

                    let successCount=0
                    console.log(this.picArr.length)
                    if(this.picArr.length>0){
                        for(let picItem of this.picArr){
                            tools.putb64(this.upload_token, picItem).then(picRes=>{
                            console.log(picRes)
                            successCount++
                            this.remark.push({name:'2',value:picRes})
                            if(successCount==this.picArr.length){
                                console.log(JSON.stringify(this.remark))
                                this.setRealizeEvaluate()
                            }
                    }).catch(picErr=>{
                    console.error(picErr)
                })
                        }
                    }else{
                        this.setRealizeEvaluate()
                    }
    			}else{
    				Toast('先说点什么吧？')
    			}
    		},
            setRealizeEvaluate(){
                let obj={
                        remark:this.remark
                    }
                    console.log(obj)
                    setRealize(obj).then(res=>{
                        //提交信息到后台
                        if(res.data.Result=="success"){
                            Toast('非常感谢您的反馈!')
                            //提交过后要跳转的地方

                            // this.$router.push('ddRepair')
                            closeWindow()
                        }
                    })
            }
		},
		created(){
			document.title="反馈意见"
		}
    }
</script>
<style scoped lang="less">
@rem:30rem;
.submitButton{
	margin-top:110/@rem;
	width:100%;
	height:100/@rem;
	line-height: 105/@rem;
	font-size: 30/@rem;
	color:#fff;
	background-color: #44b197;
	text-align: center;
}
</style>


